<template>
  <div id="app">
    <p id="head">LA<br>NUIT<br>GLAMOUR</p>
    <p id="sub-head">The Official GESA Dinner</p>

    <p id="brief">Live results of the 2019 GESA Dinner Awards voting.</p>

    <div v-if="this.pollData['departmental-votes'].length === 0" class="loader">
      LOADING...
    </div>

    <div id="box" v-else>
      <div class="category">
        <h4 class="category-title">Best Departmental President</h4>
        <div class="category-val" v-for="(v, i) in pollData['departmental-votes']" :key="i">
          <span class="category-label">{{v.name}}</span>
          <span class="category-vote">{{v.votes}}</span>
        </div>
      </div>
      <div class="category">
        <h4 class="category-title">Discovery of the year</h4>
        <div class="category-val" v-for="(v, i) in pollData['discovery-votes']" :key="i">
          <span class="category-label">{{v.name}}</span>
          <span class="category-vote">{{v.votes}}</span>
        </div>
      </div>
      <div class="category">
        <h4 class="category-title">Best Student Entrepreneur of the Year</h4>
        <div class="category-val" v-for="(v, i) in pollData['entrepreneur-votes']" :key="i">
          <span class="category-label">{{v.name}}</span>
          <span class="category-vote">{{v.votes}}</span>
        </div>
      </div>
      <div class="category">
        <h4 class="category-title">Best Dressed Female Student</h4>
        <div class="category-val" v-for="(v, i) in pollData['female-votes']" :key="i">
          <span class="category-label">{{v.name}}</span>
          <span class="category-vote">{{v.votes}}</span>
        </div>
      </div>
      <div class="category">
        <h4 class="category-title">Best Dressed Male Student</h4>
        <div class="category-val" v-for="(v, i) in pollData['male-votes']" :key="i">
          <span class="category-label">{{v.name}}</span>
          <span class="category-vote">{{v.votes}}</span>
        </div>
      </div>
      <div class="category">
        <h4 class="category-title">Most Influential</h4>
        <div class="category-val" v-for="(v, i) in pollData['influential-votes']" :key="i">
          <span class="category-label">{{v.name}}</span>
          <span class="category-vote">{{v.votes}}</span>
        </div>
      </div>
      <div class="category">
        <h4 class="category-title">Most Outstanding International Student</h4>
        <div class="category-val" v-for="(v, i) in pollData['international-votes']" :key="i">
          <span class="category-label">{{v.name}}</span>
          <span class="category-vote">{{v.votes}}</span>
        </div>
      </div>
      <div class="category">
        <h4 class="category-title">Most Versatile/Innovative</h4>
        <div class="category-val" v-for="(v, i) in pollData['versatile-polls']" :key="i">
          <span class="category-label">{{v.name}}</span>
          <span class="category-vote">{{v.votes}}</span>
        </div>
      </div>
      
      
      <!-- leading in each section -->
      <h4>Category Toppers</h4>
      <div class="leading" v-for="(x, i) in topInCategories" :key="i">
        <p class="leading-cat">{{x.category}}</p>
        <p class="leading-val">{{x.name}}</p>
      </div>

      <!-- others -->
      <!-- total number of votes -->
      # of votes(total): {{totalNumOfVotes}}
    </div>

  </div>
</template>

<script>
import firebase from 'firebase/app'
import 'firebase/firestore'

let config = {
  apiKey: "AIzaSyADS19XDKybFZbcyHAs0L8C5qzOkMyqlQk",
  authDomain: "gesadinnerawards-9ec4f.firebaseapp.com",
  databaseURL: "https://gesadinnerawards-9ec4f.firebaseio.com",
  projectId: "gesadinnerawards-9ec4f",
  storageBucket: "gesadinnerawards-9ec4f.appspot.com",
  messagingSenderId: "683866104476"
};
firebase.initializeApp(config);


let db = firebase.firestore()

export default {
  name: 'app',
  components: {
    // Results: require('@/components/Results.vue'),
  },
  data: () => ({
    loading: true,
    selectCat: 'influential-votes',
    pollData: {
      'departmental-votes': [],
      'discovery-votes': [],
      'entrepreneur-votes': [],
      'female-votes': [],
      'influential-votes': [],
      'international-votes': [],
      'male-votes': [],
      'versatile-polls': [],
    },
    chartOptions: {},
  }),
  computed: {
    totalNumOfVotes(){
      if(this.pollData['departmental-votes'].length === 0) return 0
      let n = 0;
      for(let k in this.pollData){
        this.pollData[k].forEach((o) => {
          n += o.votes
        })
      }
      return n;
    },
    topInCategories(){
      if(this.pollData['departmental-votes'].length === 0) return []
      // [
      //    {
      //       category: '', name: ''
      //    },
      // ]
      // 
      let l = [];
      for(let k in this.pollData){
        let t = {
          'category': k,
        }
        let h = this.pollData[k][0].votes
        let n = this.pollData[k][0].name
        this.pollData[k].forEach((o) => {
          if(o.votes > h){
            h = o.votes
            n = o.name
          }
        })
        t.name = n
        l.push(t)
      }
      return l;
    },
  },
  methods: {
    trackPolls(){
      db.collection('/departmental-votes').onSnapshot((snap) => {
        let t = []
        snap.forEach((d) => {
          t.push(d.data())
        })
        this.pollData['departmental-votes'] = t
      })
      db.collection('/discovery-votes').onSnapshot((snap) => {
        let t = []
        snap.forEach((d) => {
          t.push(d.data())
        })
        this.pollData['discovery-votes'] = t
      })
      db.collection('/entrepreneur-votes').onSnapshot((snap) => {
        let t = []
        snap.forEach((d) => {
          t.push(d.data())
        })
        this.pollData['entrepreneur-votes'] = t
      })
      db.collection('/female-votes').onSnapshot((snap) => {
        let t = []
        snap.forEach((d) => {
          t.push(d.data())
        })
        this.pollData['female-votes'] = t
      })
      db.collection('/influential-votes').onSnapshot((snap) => {
        let t = []
        snap.forEach((d) => {
          t.push(d.data())
        })
        this.pollData['influential-votes'] = t
      })
      db.collection('/international-votes').onSnapshot((snap) => {
        let t = []
        snap.forEach((d) => {
          t.push(d.data())
        })
        this.pollData['international-votes'] = t
      })
      db.collection('/male-votes').onSnapshot((snap) => {
        let t = []
        snap.forEach((d) => {
          t.push(d.data())
        })
        this.pollData['male-votes'] = t
      })
      db.collection('/versatile-polls').onSnapshot((snap) => {
        let t = []
        snap.forEach((d) => {
          t.push(d.data())
        })
        this.pollData['versatile-polls'] = t
      })
    }
  },
  created(){
    // get live data from DB
    this.trackPolls()
    this.loading = false
  },
}
</script>

